/* Work.css */
.work-section {
  padding: 4rem 2rem;
  background: var(--secondary-dark);
  color: var(--text-light);
  text-align: center;
}

.work-title {
  font-size: 2.5rem;
  font-family: "Times New Roman", serif;
  margin-bottom: 2rem;
  color: var(--accent-gold);
  text-transform: uppercase;
  letter-spacing: 2px;
}

.work-gallery {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
  padding: 1rem;
}

.work-item {
  background: var(--dark-brown);
  border-radius: 15px;
  overflow: hidden;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  position: relative;
}

.work-item:hover {
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.5);
}

.work-item h3 {
  font-size: 1.75rem;
  color: var(--accent-gold);
  margin-bottom: 1rem;
}

.work-item p {
  font-size: 1rem;
  color: var(--light-gold);
  opacity: 0.8;
}