/* Footer.css */
.footer {
  padding: 1.5rem 1rem; /* Reduced padding */
  background: var(--primary-dark); /* #2B221C */
  color: var(--light-gold); /* #D6B58C */
  text-align: center;
  border-top: 1px solid var(--light-brown); /* #453025 */
}

.footer p {
  font-size: 0.875rem; /* Smaller text */
  margin: 0;
  opacity: 0.9;
}

.footer a {
  color: var(--accent-gold); /* #E6B07C */
  text-decoration: none;
  transition: color 0.3s ease;
}

.footer a:hover {
  color: var(--light-gold); /* #D6B58C */
}